body {
  margin: 0;
  font-family: Arial, sans-serif;
  overflow: hidden; /* إخفاء التمرير الافتراضي */
  height: 20px;
}

.o-scroll {
  position: relative;
  height: 7000px;
  overflow: hidden;
}

.c-header {
  background: #333;
  color: #fff;
  padding: 20px;
  text-align: center;
  height: 1000px;
  z-index: 2;
  position: relative;
}

.c-section {
  height: 100vh; /* جعل كل قسم يملأ الشاشة */
  display: flex;
  align-items: center;
  justify-content: center;
  background: #333;
  color: #fff;
}
.c-section img {
  position: relative;
  z-index: 0;
  top: 50px;
  width: 100%;
}
