.c-content {
  width: 100%;
  text-align: center;
}
.nav {
  display: flex;
  justify-content: space-around;

  width: 100%;
  align-items: center;
  height: 103px;
  position: absolute;
}
.nav li {
  list-style: none;
}
.logo {
  width: 140px;
  transform: rotate(0deg);
  transition: 1s;
}
.logo:hover {
  transform: rotate(360deg);
}

.ulnavcenter {
  display: flex;
  gap: 50px;
  list-style: none;
  color: rgb(158, 158, 158);
  font-size: xx-large;
  text-decoration: none;
  flex-wrap: wrap;
  background-color: #ffffff2c;
  height: 80px;
  align-items: center;
  position: relative;
  padding-right: 28px;
  border-radius: 5px;
}
.ulnavcenter a {
  color: rgb(163, 163, 163);
  text-decoration: none;
  font-family: "El Messiri", sans-serif;
  font-optical-sizing: auto;
  font-weight: weight;
  font-style: normal;
  font-size: 23px;
  transition: 0.4s;
}
.ulnavcenter a:hover {
  color: #fff;
}
.ulnavright {
  display: flex;
  gap: 30px;
}
.ulnavright svg {
  font-size: 40px;
  cursor: pointer;
  color: white;
  transform: rotate(0deg);
  transition: 1s;
}
.ulnavright svg:hover {
  color: rgb(111, 214, 255);
  transform: rotate(360deg);
}
.o-scroll {
  overflow: hidden; /* تأكد من أن يحتوي عنصر التمرير على هذه الخاصية */
}

[data-scroll-container] {
  height: 100%; /* تأكد من أن يكون للحاوية ارتفاع كامل */
}
.bg10 {
  width: 100%;
}
.logoball {
  width: 100px;
  transition: 1s;
  top: -8px;
  position: relative;
  opacity: 20%;
  cursor: crosshair;
}
.logoball:hover {
  opacity: 1;
}
.cculcenternav {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1px;
  transition: 0.5s;
}
.cculcenternav:hover {
  gap: 20px;
}
